.item-catalog-teaser1 {
  display: flex;
  height: 213px;
  flex-flow: row wrap;
  overflow: clip;
  outline: 1px solid #cdcdcd;
  border: 0px solid transparent;
}

.item-catalog-teaser1:hover:before {
  background: #BB3939;
}

.item-catalog-teaser1:hover {
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  z-index: 2;
}

.item-catalog-teaser1:hover .item__title h3 {
  color: #AE2121;
}

.item-catalog-teaser1:hover .item__link img {
  transform: scale(1.25);
  transition: ease-in-out 0.3s;
}

.item-catalog-teaser1:before {
  content: '';
  width: 100%;
  height: 50%;
  position: absolute;
  background: #F8F8F8;
  top: 0;
}

.item-catalog-teaser1:after {
  content: '';
  width: 100%;
  height: 50%;
  position: absolute;
  background: #fff;
  bottom: 0;
}

.item-catalog-teaser1 .item__link {
  padding: 23px 0 0;
  flex-flow: row wrap;
  z-index: 1;
  display: flex;
  border: 0;
  flex: 1;
}

.item-catalog-teaser1 .item__link .item__title {
  flex: 1 100%;
}

.item-catalog-teaser1 .item__link .item__title h3 {
  font-size: 15px;
  margin: 0 10px 0;
  text-align: center;
  line-height: 20px;
  font-weight: 500;
}

.item-catalog-teaser1 .item__image {
  height: 100px;
  position: relative;
  display: flex;
  flex: 1 100%;
}

.item-catalog-teaser1 .item__image img {
  width: 100%;
  object-fit: contain;
  /*! margin: 0 auto; */
}

@media only screen and (min-width: 768px) {
  .item-catalog-teaser1 {
    height: 301px;
  }

  .item-catalog-teaser1 .item__link {
    padding: 40px 0 0;
  }

  .item-catalog-teaser1 .item__image {
    height: 158px;
  }
}

@media only screen and (min-width: 1024px) {
  .item-catalog-teaser1 {
    height: 380px;
  }

  .item-catalog-teaser1 .item__link {
    padding: 45px 0 0;
  }

  .item-catalog-teaser1 .item__link .item__title h3 {
    font-size: 20px;
    line-height: 28px;
  }

  .item-catalog-teaser1 .item__image {
    height: 200px;
  }
}